        настоящей оферты, Стороны будут разрешать путем переговоров.
      </p>
      <p class="offer__description-p">
        <strong>6.5.</strong> В случае невозможности урегулирования споров путём
        переговоров они подлежат разрешению в соответствии с действующим
        законодательством Российской Федерации.
      </p>
      <vue-title-text :title-params="h2Params7" />
      <p class="offer__description-p">
        <strong>7.1.</strong> Соглашаясь с условиями оферты в настоящей
        редакции, Заказчики выражают своё согласие на получение информационных
        сообщений о текущих изменениях в работе Службы, которые касаются
        взаимоотношений Службы с Заказчиками.
      </p>
      <p class="offer__description-p">
        <strong>7.2.</strong> Соглашаясь с условиями оферты в настоящей
        редакции, Партнёры выражают своё согласие на получение информационных
