  <vue-widget-fixed v-if="regPage">
    <div class="offer__check">
      <p class="offer__check-p">С оффертой ознакомлен!</p>
      <vue-check-box
        :value="checkboxState"
        @change="updateStateCheckbox($event)"
      />
    </div>
    <p class="offer__check-p offer__back" @click="goToRegister">
      Вернуться назад
    </p>
  </vue-widget-fixed>
</template>
<script lang="ts" setup>
import { ref } from "vue";
import VueTitleText from "@/components/Titles/VueTitleText.vue";
import { TitleParams } from "@/interfaces/TitleParamsInterface";
import VueCheckBox from "@/components/CheckBoxs/VueCheckBox.vue";
import VueWidgetFixed from "@/components/Widgets/VueWidgetFixed.vue";
import { useRouter, useRoute } from "vue-router";

const checkboxState = ref(false);

const router = useRouter();
const route = useRoute();

const regPage = ref(false);

const updateStateCheckbox = (ev: any) => {
  checkboxState.value = ev;
};

if (route.query.reg) {
  regPage.value = true;
}
if (route.query.ch) {
  if (route.query.ch === "true") {
    checkboxState.value = true;
  } else if (route.query.ch === "false") {
    checkboxState.value = false;
  }
}

const h1Params: TitleParams = {
  title: "Оферта",
  tag: "h1",
  size: 45,
  dimension: "px",
};

const goToRegister = () => {
  router.push({
    path: "registration",
    query: {
      ...router.currentRoute.value.query,
      ch: checkboxState.value.toString(),
    },
  });
};

const h2Params: TitleParams = {
  title: "1. Общие положения",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params2: TitleParams = {
  title: "2. Предмет оферты",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params3: TitleParams = {
  title: "3. Порядок оказания услуг",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params4: TitleParams = {
  title:
    "4. Осуществление расчётов между Заказчиком и Партнёром, Заказчиком и Службой",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params5: TitleParams = {
  title: "5. Ответственность Сторон",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params6: TitleParams = {
  title: "6. Порядок изменения и расторжения договора, разрешения споров",
  tag: "h2",
  size: 30,
  dimension: "px",
};
const h2Params7: TitleParams = {
  title: "7. Дополнительные условия",
  tag: "h2",
  size: 30,
  dimension: "px",
};
</script>
