ии «Арбайт», а также в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1». </p><p class=\"offer__description-p\"><strong>1.3.</strong> Заказчики и Партнёры должны внимательно ознакомиться с текстом настоящей оферты. В случае если Заказчик или Партнёр не согласен с какими-либо её условиями, ему предлагается отказаться от услуг Службы. </p><p class=\"offer__description-p\"><strong>1.4.</strong> Определения, используемые в целях выполнения условий настоящей оферты: </p><p class=\"offer__description-p\"><strong>1.4.1.</strong> Услуги — информационные услуги, предоставляемые Службой, направленные на прием информации о заказе от Заказчика; обработку и передачу информации о заказе Партнёру; информирование Заказчика о Партнёре, выбравшем заказ к исполнению. Служба не оказывает Заказчикам услуги по грузоперевозкам, погрузо-разгрузочным работам, услуги разнорабочих, услуги доставки, услуги выноса и вывоза мусора. Служба не оказывает Заказчикам и Партнёрам никаких иных услуг, кроме информационных услуг. </p><p class=\"offer__description-p\"><strong>1.4.2.</strong> Заказ — внесённая в информационную базу Службы информация о заказе Заказчика на услуги, оказываемые Партнёрами. </p><p class=\"offer__description-p\"><strong>1.4.3.</strong> Партнёр — лицо, самостоятельно, своими силами оказывающее Заказчику услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора. </p><p class=\"offer__description-p\"><strong>1.4.4.</strong> Заказчик — лицо, осуществляющее заказ услуг Партнёров по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора, с информационной помощью Службы. </p><p class=\"offer__description-p\"><strong>1.4.5.</strong> Маршрут — путь следования транспортного средства между пунктами отправления и назначения. </p><p class=\"offer__description-p\"><strong>1.4.6.</strong> Информацион