услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора. Регистрация Партнёра в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1» в качестве лица, оказывающего услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора, подтверждает полное и безоговорочное принятие условий настоящей оферты. Регистрация Партнёра в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1» осуществляется посредством предоставления сведений о себе удалённо с использованием сети Интернет. Предоставление сведений Партнёром удалённо с использованием сети Интернет осуществляется путём направления Партнёром со своего телефонного номера с помощью Интернет-мессенджера WhatsApp на телефонный номер Службы электронного сообщения с подтверждением принятия условий настоящей оферты, а также с сообщением своих данных в следующем объёме: фамилия, имя, отчество, дата рождения, адрес места жительства, номер сотового телефона. В процессе регистрации Служба может попросить Партнёра предоставить документы (электронные образы (сканы) документов), подтверждающие достоверность указанных сведений. Регистрация Партнёра в Мобильном приложении «Арбайт» или в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1» осуществляется по усмотрению Службы. Предоставление документов и сведений, необходимых для регистрации, не влечет за собой безусловную обязанность Службы произвести регистрацию Партнёра. Процедура регистрации завершается и подтверждается внесением в информационную базу Службы информации о Партнёре, как о лице самостоятельно, своими силами оказывающему услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора. Копии и (или) электронные образы документов в процессе регистрации Служба истребует исключительно в целях проверки достоверности предоставляемых сведений и не сохраняет после завершения процедуры регистрации.</strong></p><p class=\"offer__description-p\"><strong>3.3.</strong> Заказчик несёт ответственность за содержание и достоверность информации, предоставленной при размещении заказа. </p><p class=\"offer__description-p\"><strong>3.4.</strong> Партнёр несёт ответственность за содержание и достоверность информации, предоставленной при регистрации в Мобильном приложении «Арбайт» в качестве Партнёра, оказывающего услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора. <strong>Партнёр несёт ответственность за содержание и достоверность информации, предоставленной при регистрации Партнёра в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1» в качестве лица, оказывающего услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора.</strong></p><p class=\"offer__description-p\"><strong>3.5.</strong> Принимая условия настоящей оферты, Партнёр осознаёт, что информация, доступ к которой он получил в рамках исполнения данной оферты, используется им самостоятельно, в своих интересах и под свою ответственность. </p><p class=\"offer__description-p\"><strong>3.6.</strong> Принимая условия настоящей оферты, Партнёр в соответствии с частью 1 статьи 152.1 Гражданского кодекса Российской Федерации даёт Службе согласие на обнародование и дальнейшее использование своего изображения (фотографии) в целях предоставления его Заказчику в составе информации о Партнёре, принявшем Заказ к исполнению. </p><p class=\"offer__description-p\"><strong>3.7.</strong> Служба на возмездной основе размещает информацию о заказе Заказчика в Мобильном приложении «Арбайт», а также в группе мессенджера «WhatsApp» «Арбайт грузчики/рабочие 1» при этом по окончании оказания услуги Заказчику Партнёром Заказчик оплачивает информационные услуги Службы в определённом Службой размере процента от общей суммы оплаты Партнёру от Заказчика за исполнение заказа. Размер оплаты за информационные услуги Службы, а также размер оплаты за услуги Партнёра, сообщается Заказчику после размещения заказа и принятия его к исполнению Партнёром, но до оказания услуги Партнёром. В случае несогласия с суммой оплаты информационных услуг Службы и (или) с суммой оплаты услуг Партнёра Заказчик вправе немедленно отказаться от заказа. Не допускается отказ от заказа по причине несогласия с размером оплаты после оказания Заказчику услуги Партнёром. Служба безвозмездно предоставляет доступ к информации о заказе Партнёру, который принимает заказ к исполнению, уведомляет о своем выборе Службу. При этом Служба гарантирует Заказчику и Партнёру исключительно оказание вышеуказанных информационных услуг. </p><p class=\"offer__description-p\"><strong>3.8.</strong> Служба имеет право отказать в предоставлении информационных услуг Заказчикам и Партнёрам, выражающим несогласие с условиями настоящей оферты, или не исполняющим (ненадлежащее исполняющим) её условия. </p><p class=\"offer__description-p\"><strong>3.9.</strong> В целях внутреннего контроля деятельности Службы и контроля качества предоставления Службой информационных услуг переговоры между Заказчиком и Службой, Партнёром и Службой могут записываться. </p><p class=\"offer__description-p\"><strong>3.10.</strong> Заказчик может проинформировать Службу о случаях нарушения Партнёром своих прав. В этом случае Служба может безвозмездно оказать Заказчику и Партнёру дополнительные информационные услуги, а именно: уведомляет Партнёра о поступившей от Заказчика претензии и о её содержании, сообщает Партнёру контактный номер телефона Заказчика, сообщает Заказчику контактный номер телефона Партнёра. </p><p class=\"offer__description-p\"><strong>3.11.</strong> При сборе и обработке персональных данных Заказчиков и Партнёров Служба руководствуется положениями Федерального закона от 27 июля 2006 года № 152 «О персональных данных». Согласно п.5 ч.1 ст.6 данного Федерального закона, не требуется получать согласие Заказчиков и Партнёров на сбор и обработку их персональных данных Службой, так как персональные данные Заказчиков и Партнёров собираются и обрабатываются Службой только в целях исполнения обязательств Службы перед Заказчиками и Партнёрами, а также обязательств Заказчиков и Партнёров перед Службой, возникших на основе акцепта настоящей оферты. Персональные данные собираются и обрабатываются Службой в следующем объёме: фамилия, имя, отчество, дата рождения, адрес места жительства, номер сотового телефона. </p><p class=\"offer__description-p\"><strong>3.12.</strong> Деятельность Службы по сбору, хранению, систематизации информации, формированию информационной базы Службы, а также правоотношения между Заказчиком и Службой, Партнёром и Службой по предоставлению доступа к информационной базе Службы осуществляются в соответствии с положениями Федерального закона от 27.07.2006 г. № 149-ФЗ «Об информации, информационных технологиях и о защите информации». </p><p class=\"offer__description-p\"><strong>3.13.</strong> В соответствии со ст. 7, ст. 9 Федерального закона «Об информации, информационных технологиях и о защите информации» информация, размещенная в информационной базе Службы, является общедоступной, свободно распространяемой. Информация не отнесена действующим законодательством Российской Федерации к ограниченной или запрещённой к распространению. </p><p class=\"offer__description-p\"><strong>3.14.</strong> Информационную базу Службы образуют следующие материалы: Актуальная информация о существующей потребности в услугах по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услугах разнорабочих. </p><p class=\"offer__description-p\"><strong>3.15.</strong> Материалы, образующие информационную базу Службы, имеют следующие характеристии: </p><p class=\"offer__description-p\"><strong>3.15.1.</strong> Данные о существующей потребности в услуге по грузоперевозкам: - адрес места подачи автомобиля; - вид груза; - адрес места окончания поездки или расстояние доставки; - тариф Партнёра, принявшего заказ к исполнению; - расчётная стоимость услуги Партнёра; - расчётная стоимость информационных услуг Службы; - контактная информация Заказчика. </p><p class=\"offer__description-p\"><strong>3.15.2.</strong> Данные о существующей потребности в услуге по погрузо-разгрузочным работам: - адрес (адреса) погрузо-разгрузочных работ; - вид груза; - тариф Партнёра, принявшего заказ к исполнению; - расчётная стоимость услуги Партнёра; - расчётная стоимость информационных услуг Службы; - контактная информация Заказчика. </p><p class=\"offer__description-p\"><strong>3.15.3.</strong> Данные о существующей потребности в услугах разнорабочих: - вид и описание работ; - адрес производства работ; - тариф Партнёра, принявшего заказ к исполнению; - расчётная стоимость услуги Партнёра; - расчётная стоимость информационных услуг Службы; - контактная информация Заказчика. </p><p class=\"offer__description-p\"><strong>3.15.4.</strong> Данные о существующей потребности в услугах доставки: - адрес места подачи автомобиля; - вид груза; - адрес места окончания поездки или расстояние доставки; - тариф Партнёра, принявшего заказ к исполнению; - расчётная стоимость услуги Партнёра; - расчётная стоимость информационных услуг Службы; - контактная информация Заказчика. </p><p class=\"offer__description-p\"><strong>3.15.5.</strong> Данные о существующей потребности в услугах выноса и вывоза мусора: - вид и описание работ; - адрес производства работ; - тариф Партнёра, принявшего заказ к исполнению; - расчётная стоимость услуги Партнёра; - расчётная стоимость информационных услуг Службы; - контактная информация Заказчика. </p><p class=\"offer__description-p\"><strong>3.16.</strong> Территория, на которой предоставляется право использования информации, — Российская Федерация. </p><p class=\"offer__description-p\"><strong>3.17.</strong> Партнер самостоятельно принимает решение о регистрации в качестве субъекта предпринимательской деятельности или самозанятого, в соответствии с действующим законодательством Российской Федерации, а также обеспечивает соблюдение требований действующего законодательства Российской Федерации, регулирующего деятельность в сфере оказания соответствующего вида Услуг. </p>", 22)),
        _createVNode(VueTitleText, { "title-params": h2Params4 }),
        _cache[5] || (_cache[5] = _createStaticVNode("<p class=\"offer__description-p\"><strong>4.1.</strong> Служба предоставляет Заказчику услуги, предмет которых определён настоящей офертой, на возмездной основе. </p><p class=\"offer__description-p\"><strong>4.2.</strong> Расчёт за оказание услуг по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуг разнорабочих осуществляется непосредственно между Заказчиком и Партнёром. Служба информирует Заказчика о стоимости оказания услуг Партнёра и о стоимости оказания информационных услуг Службы. Стоимость услуг Партнёра рассчитывается на основании тарифов, применяемых Партнёром, принявшим заказ к исполнению. Информирование Заказчика об окончательной стоимости услуг Партнёра не свидетельствует и не может расцениваться как то, что услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуги разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора, предоставляются Службой. Стоимость информационных услуг Службы рассчитывается в размере процента от общей суммы оплаты Партнёру от Заказчика за исполнение заказа. По окончании оказания услуги Заказчику Партнёром Заказчик оплачивает информационные услуги Службы в вышеуказанном размере. Размер оплаты за услуги Партнёра, а также размер оплаты за информационные услуги Службы сообщаются Заказчику после размещения заказа и принятия его к исполнению Партнёром, но до оказания услуги Заказчику Партнёром. В случае несогласия с суммой оплаты услуг Партнёра и (или) с суммой оплаты информационных услуг Службы Заказчик вправе немедленно отказаться от заказа. Не допускается отказ от заказа по причине несогласия с размером оплаты после оказания Заказчику услуги Партнёром. </p><p class=\"offer__description-p\"><strong>4.3.</strong> Форма осуществления расчётов за оказание услуг по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуг разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора согласуется непосредственно Заказчиком и Партнёром без участия Службы. </p><p class=\"offer__description-p\"><strong>4.4.</strong> Форма осуществления расчётов за оказание информационных услуг согласуется непосредственно Заказчиком и Службой без участия Партнёра. </p><p class=\"offer__description-p\"><strong>4.5.</strong> Служба не даёт Заказчику и Партнеру каких-либо указаний относительно формы и порядка расчётов за оказанные услуги по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуг разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора. </p><p class=\"offer__description-p\"><strong>4.6.</strong> Служба может обеспечить техническую возможность для оплаты Заказчиком услуг Партнёра по грузоперевозкам и (или) погрузо-разгрузочным работам, и (или) услуг разнорабочих, и (или) услуги доставки, и (или) услуги выноса и вывоза мусора, в форме безналичного платежа с использованием сайта Службы в сети Интернет по адресу https://arbait.in/ (при наличии технической возможности) и с использованием услуг платёжного сервиса (платёжной системы) «Робокасса». </p><p class=\"offer__description-p\"><strong>4.7.</strong> Служба может обеспечить техническую возможность для оплаты Заказчиком информационных услуг Службы в форме безналичного платежа с использованием сайта Службы в сети Интернет по адресу https://arbait.in/ (при наличии технической возможности) и с использованием услуг платёжного сервиса (платёжной системы) «Робокасса». </p><p class=\"offer__description-p\"><strong>4.8.</strong> При расчётах между Заказчиком и Партнёром безналичным платежом Служба может выступать в качестве агента Партнёра, действующего от имени и за счёт Партнёра, в части приёма денежных средств Заказчика, предназначенных для оплаты оказанной Партнёром услуги, и дальнейшей передачи вышеуказанных денежных средств Партнёру. </p><p class=\"offer__description-p\"><strong>4.9.</strong> При расчётах между Заказчиком и Службой как наличными денежными средствами, так и в безналичном порядке, Партнёр может выступать в качестве агента Службы, действующего от имени Службы, в части приёма денежных средств Заказчика, предназначенных для оплаты оказанных Службой информационных услуг, и дальнейшей передачи вышеуказанных денежных средств Службе. </p><p class=\"offer__description-p\"><strong>4.10.</strong> Оплата безналичным платежом может осуществляться посредством кредитных или дебетовых карт платёжных систем «Мир», Visa, MasterCard, переводом на расчётный счёт, или иными способами, не противоречащими требованиям законодательства Российской Федерации. </p><p class=\"offer__description-p\"><strong>4.11.</strong> Оплата безналичным платежом с использованием банковских карт осуществляется в соответствии с правилами международных платёжных систем на принципах соблюдения конфиденциальности и безопасности совершения платежа. Безопасность предоставляемых Заказчиком данных обеспечивается соответствием процедур требованиям стандарта безопасности данных индустрии платёжных карт (Payment Card Industry Data Security Standard), и никто, включая Службу, не может их получить. Ввод данных банковской карты осуществляется на защищенной платёжной странице платёжного сервиса (платёжной системы) «Робокасса», обеспечивающего возможность безналичной оплаты услуг. </p><p class=\"offer__description-p\"><strong>4.12.</strong> В случае изменения Заказчиком объема заказанных Партнёру услуг (изменение маршрута, объёма погрузо-разгрузочных или иных работ, оказание Партнёром Заказчику дополнительных платных услуг и т.д.) стоимость оплаты услуг Партнёра, а также стоимость оплаты информационных услуг Службы подлежат перерасчёту. Услуги оплачиваются Заказчиком в соответствии с объёмом фактически оказанных Партнёром услуг, а также в соответствии с объёмом фактически оказанных Службой информационных услуг. </p><p class=\"offer__description-p\"><strong>4.13.</strong> Заказчик и Партнёр вправе согласовать любые формы расчётов за оказание услуг по грузоперевозкам и (или) погрузо-разгрузочным работам